<template>
  <div>
    <form>
      <p v-for="(item,key) in items">
        <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
        <input type="text" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" class="openInput">
        <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right: 0.05rem"></Icon>--> {{item.error}} </strong>
        <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
      </p>
      <p class="btn">
        <a href="javascript:void(0)" class="save" @click="save" v-if="!show">保存</a>
        <a href="javascript:void(0)" class="save" @click="change" v-if="show">修改</a>
        <a href="javascript:void(0)" @click="cancel"  class="cancel">取消</a>
      </p>
    </form>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  export default {
    data () {
      return {
        show:false,
        items:[
          {
            name:"第三方AppID",
            place:"请输入第三方AppID",
            error:'请填写第三方AppID',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"第三方AppSecret",
            place:"请输入第三方AppSecret",
            error:'请填写第三方AppSecret',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"消息校验Token",
            place:"请输入消息加解密Key",
            error:'请填写消息校验Token',
            modal:'',
            show:false,
            right:false
          },{
            name:"消息加解密Key",
            place:"请输入消息加解密Key",
            error:'请填写消息加解密Key',
            modal:'',
            show:false,
            right:false
          }
          ],
        }
    },
    methods: {
      change(){
        this.publicData(1);
      },
      publicData(val){
        let input=document.getElementsByClassName('openInput');
        if(this.items[0].modal.trim()&&this.items[1].modal.trim()&&this.items[2].modal.trim()&&this.items[3].modal.trim()){
          this.post('system/Config/setSystemOauth',{
            appid:this.items[0].modal.trim(),
            appsecret:this.items[1].modal.trim(),
            token:this.items[2].modal.trim(),
            key:this.items[3].modal.trim(),
            id:val
          }).then(res=>{
            if(res.data.code===10000){
              alert(res.data.msg_test);
            }
          })
        }else{
          if(!this.items[0].modal.trim()){
            input[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal.trim()) {
            input[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.items[2].modal.trim()){
            input[2].style.borderColor = '#e46751';
            this.items[2].show = true;
          }
          if(!this.items[3].modal.trim()){
            input[3].style.borderColor = '#e46751';
            this.items[3].show = true;
          }
        }
      },
//      取消检测
      cancelCheck(val){
        this.items[val].show=false;
      },
//      检测
      check(val){
        if(this.items[val].modal.trim()){
          this.items[val].show=false;
          this.items[val].right=true;
          event.target.style.borderColor='#EAEAEA'
        }else{
          this.items[val].show=true;
          this.items[val].right=false;
          event.target.style.borderColor='#e46751'
        }
      },
      save(){
        this.publicData();
      },
      cancel(){
          for(let i=0;i<this.items.length;i++){
            this.items[i].show=false;
            this.items[i].right=false;
            this.items[i].modal='';
          }
          let input=document.getElementsByClassName('openInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
      },
      enter(){
        event.target.style.borderColor='#6098FF'
      },
      out(val){
        switch (val){
          case 0:
              this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input(val){
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
    },
    mounted(){
      this.post("system/Config/SystemOauth",{id:1}).then(res=>{
        if(res.data.code===10000){
          this.show=true;
          this.items[0].modal=res.data.data.appid
          this.items[1].modal=res.data.data.appsecret;
          this.items[2].modal=res.data.data.token;
          this.items[3].modal=res.data.data.key;
        }
      })
    },
     components: {
      bread
    }
  }
</script>
<style scoped>
  p{
    padding: 9px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 145px;
    text-align: right;
    margin-right: 20px
  }
  p>strong{
    display: flex;
    align-items: center;
    color: #e46751;
    font-weight: 400;
    margin-left: 10px;

  }
  .must{
    font-size: 15px;
    color: red;
    margin-right: 10px;
  }

  .btn{
    padding: 20px 168px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius:5px;
    color: white;
  }
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color:#777777;
  }
  .cancel:hover{
    background:#799EE3 ;
    color: white;
    text-decoration:none;
    border: none;
  }
   input{
    display: inline-block;
    padding: 8px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius: 5px;
    background-color: #FFFFFF;
  }
</style>



